<template>
	<div id="staffContainer">
		<div class="header">
			<el-radio-group v-model="linkto" fill="#4877E7" @change="changer">
				<el-radio-button v-for="(item, index) in navName" :label="item.label" :key="index"></el-radio-button>
			</el-radio-group>
			<router-link :to="{path:'/staff/invitStaff'}">
				<el-button class="invite" v-if="showTreeTable==true">邀请员工</el-button>
			</router-link>
			<router-link :to="{path:'/staff/editDuty'}">
				<el-button class="invite" v-if="showTreeTable==false">添加职位</el-button>
			</router-link>
		</div>
		<position-management v-if="showTreeTable==false"></position-management>
		<div v-else>
			<staff-table class="staff-table"  ref="goodtable"></staff-table>
			<staff-tree class="staff-tree" ref="mertree" @margin-change="changeMargin"></staff-tree>
			<!--<el-row :gutter="280">
				<el-col :xs="2" :sm="3" :md="4" :lg="4" :xl="5"><staff-tree class="staff-tree"></staff-tree></el-col>
				<el-col :xs="22" :sm="21" :md="20" :lg="20" :xl="19"><staff-table class="staff-table"></staff-table></el-col>
 				 
			</el-row>-->
		</div>
	</div>
</template>

<script>
	import StaffTree from './staffTree';
	import StaffTable from './staffTable';
	import positionManagement from "./positionManagement";
	export default {
		data() {
			return {
				linkto: "员工管理",
				showTreeTable: true,
				navName: [{
						label: "员工管理"
					},
					{
						label: "职级管理"
					}
				]
			};
		},
		components: {
			StaffTree,
			StaffTable,
			positionManagement
		},
		created() {
			if(sessionStorage.getItem("staffLabel") == null) {
				if(this.$route.query.name !== undefined) {
					this.linkto = this.navName[1].label;
					this.showTreeTable = false;
				}
			} else {
				this.linkto = sessionStorage.getItem("staffLabel");
				this.changer();
			}

		},
		methods: {
			changer() {
				if(this.linkto == this.navName[1].label) {
					this.showTreeTable = false;
				} else {
					this.showTreeTable = true;
				}
				sessionStorage.setItem("staffLabel", this.linkto);
			},
			changeMargin() {
		      setTimeout(() => {
		        let x = this.$refs.mertree.$el.clientWidth;
		        if (x != 70) {
		          if (this.$refs.goodtable) {
		            this.$refs.goodtable.$el.style.marginLeft = x + 14 + "px";
		          }
		          if (this.$refs.acctable) {
		            this.$refs.acctable.$el.style.marginLeft = x + 14 + "px";
		          }
		        }
		      }, 20);
		}
			}
	};
</script>

<style lang="scss" scoped>
	@import 'src/styles/mixin.scss';
	#staffContainer {
		position: relative;
		@include wh(100%, 100%);
	}
	
	.header {
		
		width: 100%;
		height: 50px;
		line-height: 50px;
		padding: 0 14px;
		background: #FFFFFF;
		margin-bottom: 14px;
	}
	
	.invite {
		@include wh(80px, 33px);
		border: 1px solid #4877e7;
		color: #4877e7;
		border-radius: 0;
		position: absolute;
		top: 8px;
		right: 14px;
	}
	
	.staff-tree {
		/*min-width: 290px;*/
		position: absolute;
		top: 64px;
		bottom: 0;
		left: 0;
		background-color: #fff;
	}
	
	.staff-table {
		margin-left: 300px;
		background-color: #fff;
	}
</style>
<style lang="scss">
	@import "src/styles/mixin.scss";
	#staffContainer {
		.el-radio-group .el-radio-button {
			margin-right:80px;
			color: #4877E7;
		}
		.el-radio-button .el-radio-button__inner {
			width: 80px;
			height: 33px;
			padding: 9px;
			border: none;
			/*border: 1px solid #4877E7;*/
		}
		.el-radio-button:first-child .el-radio-button__inner,
		.el-radio-button:last-child .el-radio-button__inner {
			border-radius: 4px;
		}
	}
</style>


